/* Global Styles */
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  background-color: #f9fafb;
  color: #333;
  line-height: 1.6;
}

/* Header Styles */
header {
  background: rgba(255, 255, 255, 0.8); /* Glassmorphism background */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px); /* Adds the glass effect */
  position: sticky; /* Sticks to the top when scrolling */
  top: 0;
  z-index: 1000;
}

header nav {
  /* display: flex; */
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

/* Center Align List */
header nav ul {
  display: flex;
  justify-content: center; /* Center navigation items */
  align-items: center;
  gap: 2rem; /* Space between items */
  list-style: none;
  padding: 0;
  margin: 0;
}

header nav ul li {
  position: relative;
}

header nav ul li a {
  text-decoration: none;
  font-size: 1.1rem;
  font-weight: 500;
  color: #333;
  padding: 1rem 4rem;
  border-radius: 5px;
  transition: all 0.3s ease;
  /* border: solid; */
}

header nav ul li a:hover {
  background: linear-gradient(90deg, #6a11cb, #2575fc);
  color: white;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

/* Fancy Hover Effect Underline */
header nav ul li::after {
  content: '';
  position: absolute;
  bottom: -3px;
  left: 50%;
  width: 0;
  height: 2px;
  background: linear-gradient(90deg, #6a11cb, #2575fc);
  transition: width 0.3s ease, left 0.3s ease;
}

header nav ul li:hover::after {
  width: 100%;
  left: 0;
}

/* Main Content Styles */
main {
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

/* Responsive Design */
@media (max-width: 768px) {
  header nav {
    flex-direction: column; /* Stack the navigation vertically */
  }

  header nav ul {
    flex-direction: column; /* Stack the navigation links */
    gap: 1rem; /* Adjust spacing for smaller screens */
  }

  header nav ul li {
    text-align: center; /* Center text alignment */
  }
}

@media (max-width: 480px) {
  header nav {
    padding: 1rem;
  }

  header nav ul li a {
    font-size: 1rem; /* Reduce font size for small screens */
    padding: 0.5rem;
  }
}
